.game-page {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    overflow: hidden;
}

    .machine-hand, .user-hand {
        display: flex;
        justify-content: center;
        height: 45%;
    }

    .machine-hand {
        transform: rotate(180deg);
    }